<template>
  <div class="container">
    <div class="header">
      <div class="title">设备录入</div>
      <el-upload
        style="margin-left: 30px"
        accept=".xlsx"
        :action="uploadUrl"
        :show-file-list="false"
        :headers="uploadHeader" :on-success="handleSuccess">
        <el-button size="small" type="primary">点击上传</el-button>
      </el-upload>
    </div>
    <div class="content">
      <el-row>
        <el-form ref="form" :model="form" status-icon label-width="100px" @submit.native.prevent>
          <el-form-item label="公司ID" prop="companyId">
            <el-input v-model="form.companyId" size="medium" placeholder="请填写公司ID" />
          </el-form-item>
          <el-form-item label="公司名" prop="companyName">
            <el-input v-model="form.companyName" size="medium" placeholder="请填写公司名" />
          </el-form-item>
          <el-form-item label="设备ID" prop="deviceId">
            <el-input v-model="form.deviceId" size="medium" placeholder="请填写设备ID" />
          </el-form-item>
          <el-form-item label="云ID" prop="cloudId">
            <el-input v-model="form.cloudId" size="medium" placeholder="请填写云ID" />
          </el-form-item>
          <el-form-item label="出厂日期" prop="deliveryTime">
            <el-date-picker v-model="form.deliveryTime" type="date" value-format="timestamp" placeholder="选择出厂日期" />
          </el-form-item>
          <el-form-item label="保修期" prop="warranty">
            <el-date-picker v-model="form.warranty" type="date" value-format="timestamp" placeholder="选择保修期" />
          </el-form-item>
          <el-form-item class="submit">
            <el-button type="primary" @click="submitForm('form')">保 存</el-button>
            <el-button @click="resetForm('form')">重 置</el-button>
          </el-form-item>
        </el-form>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import DeviceModel from '@/models/device'
import config from '../../config'
import { getToken } from '../../utils/cookie'

export default {
  name: 'DeviceInput',
  components: {},
  data() {
    return {
      uploadUrl: `${config.baseURL}cms/device/upload`,
      uploadHeader: {
        Authorization: getToken()
      },
      form: {
        companyName: '', // 公司名
        companyId: '', // 公司ID
        deviceId: '', // 设备ID
        cloudId: '', // 云ID
        deliveryTime: '', // 出厂日期
        warranty: '', // 保修期
      },
    }
  },
  computed: {},
  created() { },
  mounted() { },
  methods: {
    // 提交表单
    submitForm(formName) {
      console.log('form', this.form)
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    handleSuccess(response, file, fileList) {
      if (response.error_code === 1) {
        this.$message.success('文件上传成功')
      } else {
        this.$message.error(response.msg)
      }
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.container {
  .header {
    display flex
    align-items center
    .title {
      height 59px
      line-height 59px
      font-size 16px
      font-weight 500
    }
  }
}
</style>
